<template>
    <div class="delivery text-center">
        <h4 v-text="text" v-color></h4>
    </div>
</template>
<script>
export default {
    data(){
        return{
          text:"对不起本店暂时没有快递！！！"
        }
    },
    created(){
         this.run()
    },
    methods:{
        run(){
        setInterval(()=>{ 
         var statr=this.text.substring(0,1)
         var end=this.text.substring(1)
         this.text=end+statr},500)
        }
    },
    directives:{
        "color":{
            bind(el){
                el.style.color="#"+Math.random().toString().substring(2,8);
            }
        }
    }
  
}
</script>
<style lang="scss" scoped>
  .delivery{
     background: rgba(0,0,0,.4);
     border-radius: 10px;
     margin-top: 30px;
     padding: 10px 0;
  }
</style>




