<template>
    <div class="contact">
        <div class="card">
          <div class="card-header">联系我们</div>
          <div class="card-body">
          <h4 class="card-title">联系我们</h4>
          <p class="card-text">1263455889@qq.com</p>
          <router-link :to="{name:'personnamelink'}">联系人</router-link>
          <router-link :to="{name:'tellink'}">电话</router-link>
          <transition name="my" mode="out-in">
              <router-view></router-view>
          </transition>
          </div>
          </div>
    </div>
</template>
<script>
export default {
    
}
</script>
<style lang="scss" scoped>
.contact{
.my-enter,
.my-leave-to{
    opacity: 0;
    transform:translateX(100px)
}
.my-enter-active,
.my-leave-active{  
    transition: all .6s linear;
}
 .actives{
     color: black;
     font-weight:bold;
 }
}
</style>




