<template>
    <div class="addpiaa">
      <form @submit.prevent>
          <h3>添加PIAA</h3>
          <div class="form-group row">
              <label class="col-sm-2">品种</label>
               <label class="col-sm-10">
                   <input type="text" class="form-control" v-model="newpiaa.name" v-mofu> 
               </label>
          </div>
          <div class="form-group row">
              <label class="col-sm-2">描述</label>
               <label class="col-sm-10">
                   <textarea rows="4" class="form-control" v-model="newpiaa.description"></textarea>
               </label>
          </div>
         <p><strong>选项1：</strong></p>
          <div class="form-group row">
              <label class="col-sm-2">尺寸</label>
               <label class="col-sm-10">
                   <input type="text" class="form-control" v-model="newpiaa.size1">
               </label>
          </div>
           <div class="form-group row">
              <label class="col-sm-2">价格</label>
               <label class="col-sm-10">
                   <input type="text" class="form-control" v-model="newpiaa.price1">
               </label>
          </div>
           <p><strong>选项2：</strong></p>
          <div class="form-group row">
              <label class="col-sm-2">尺寸</label>
               <label class="col-sm-10">
                   <input type="text" class="form-control" v-model="newpiaa.size2">
               </label>
          </div>
           <div class="form-group row">
              <label class="col-sm-2">价格</label>
               <label class="col-sm-10">
                   <input type="text" class="form-control" v-model="newpiaa.price2">
               </label>
          </div>
          <div><button class="btn btn-block btn-success" @click="add">添加</button></div>
      </form>
    </div>
</template>
<script>
export default {
    data(){
        return{
            newpiaa:{
                size2:'',
                size1:'',
                price1:'',
                price2:'',
                description:'',
                name:''
            },
            
        }
    },
    methods:{
        add(){
           let dataform={
                name:this.newpiaa.name,
                description:this.newpiaa.description,
                options:[
                    {size1:this.newpiaa.size1,price1:this.newpiaa.price1},
                    {size1:this.newpiaa.size2,price1:this.newpiaa.price2}
                ]
            
           }
           let dataform2={
                name:this.newpiaa.name,
                description:this.newpiaa.description,
                options:[
                    {size1:this.newpiaa.size1,price1:this.newpiaa.price1}
                ]
            
           }
        
          if(this.newpiaa.name!=""&&this.newpiaa.description!=""){
            if((this.newpiaa.size1!=""&&this.newpiaa.price1!="")&&(this.newpiaa.size2!=""&&this.newpiaa.price2!="")){
                //  this.axios.post("api/menu.json",dataform).then(res=>{console.log(res)})
                if(this.$store.commit('addfoots',dataform)){
                    alert("已有相同物种的piaa！！！")
                }
            }else
             if((this.newpiaa.size1!=""&&this.newpiaa.price1!="")||(this.newpiaa.size2!=""&&this.newpiaa.price2!="")){
                //  this.axios.post("api/menu.json",dataform2).then(res=>console.log(res))
                if(this.$store.commit('addfoots',dataform2)){
                    alert("已有相同物种的piaa！！！")
                }
            }
         }
            
        }
    },
    directives:{
        'mofu':{
            inserted(el){
               el.focus();
             
            }
        }
    }
   
}
</script>

<style lang="scss" scoped>
.addpiaa{
    label{
    text-align: center;
}
p strong{
    display: block;
    margin-left: 26px;
}
h3{
 margin-left: 30PX;
 margin-bottom: 10px;
 font-weight: bold;
}
}
</style>




