<template>
    <div class="login">
        <div class="row mt-2">
            <div class="col-lg-12 col-md-12 border nk">
                <div class="text-center card-body">
                 <img src="../assets/logo.png">
                </div>
                <form @submit.prevent>
                <label class="text-left">邮箱</label>
                <input type="email" v-model="username" placeholder="chenkai@qq.com">
                <label class="text-left">密码</label>
                <input type="password" v-model="Password" placeholder="123">
                <button @click="logins" class="btn btn-block btn-success text-center">登录</button>
                </form>
            </div>
        </div>
    </div>
</template>
<script>
// import axios from 'axios'
export default {
    data(){
        return{
            username:'',
            Password:'',
            
        }
    },
    methods:{
     logins(){
         this.axios.get("api/users.json").then(res=>{
            const user= res.data
            const arr=[]
         for(let key in user){
             arr.push(user[key]) 
         }
         if(arr.some((user)=>{
            return user.username==this.username &&user.password==this.Password
         })){
             alert("登录成功")
            this.$router.push({name:'homelink'})
            this.$store.commit('login',{"username":this.username})
         }
         })
     }
    }
}
</script>

<style lang="scss" scoped>
*{
    padding: 0;
    margin: 0;
}
.nk{
    box-sizing:border-box;
}
label{
    display: block;
    margin: 10px 0;
    font-weight:bold;
}
input{
    display: block;
    width: 100%;
    padding-left: 6px;
}
form{
    padding:10px;
}
button{
    border-radius:2px;
    margin-top: 10px;
    padding: 4px 0;
   
}
</style>




