<template>
    <div class="addshop">
        <div class="container">
            <div class="row text-center" v-for="(item,i) in shop" :key="i">
                 <div class="col-1">
                  <input type="checkbox" v-model="item.flag" @click="trunn(item)" >
                 </div>
                 <div class="col-8">
                  <div class="box">
                  <h5 class="fl">{{item.name}}piaa</h5><span>尺寸:{{item.size1}}寸</span>
                  <div class="fl">
                  <button @click="countjian(item)">-</button>
                  <strong>{{item.count}}</strong>
                  <button @click="countjia(item)">+</button></div> 
                  </div>
                  <p>小计：{{item.count*item.price1}}元</p>
                 </div>
                 <div class="col">
                 <button class="btn-outline-danger text-center" @click="delshop(item,i)">删除</button>
                 </div>
            </div>
            <div class="row text-center">
                <div class="col-3">总数：<strong>{{addcount}}</strong>件</div>
                <div class="col-6">总价：<strong>{{addpricesum}}</strong>Rem</div>
                <div class="col-3"><button class="btn btn-outline-dark" @click="post()">提交</button></div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            shop:this.$store.getters.getaddshop
        }
    },
    created(){
      
    },
    methods:{
        countjia(items){
              this.$store.commit('shopadd',items)
        },
        countjian(items){
            this.$store.commit('shopshin',items)
        },
        delshop(delitem,i){
           if(confirm('确定删除吗？？？亲！！！')) {
            this.$store.commit('delshop',delitem)
           }
        },
        post(){
            let obtshop={}
            this.shop.forEach((items,i)=>{
                if(items.flag){
                    items.times=new Date()
                    obtshop[i]=items
                }
            })
            if(JSON.stringify(obtshop)!="{}" ){
                 this.axios.post("api/postshop.json",obtshop)
                .then(res=>{
                alert("提交成功！！！！")
                 for(let key in obtshop){
                if(obtshop[key].flag){
                    obtshop[key].flag=false
                }
            }
            this.$store.commit('trueorfalse',obtshop)
           })
            }else{
                alert("请勾选商品")
            //    this.obtshop.forEach(item=>{
            //         if(item.flag){
            //             item.flag=false
            //         }
            //     })
            }
        },
        trunn(check){
          this.$store.commit('truns',check)
        }
    },
    computed:{
        addcount(){//获取总的数量
            let countsum=0
            this.shop.forEach(element => {
                if(element.flag){
                    countsum+=parseInt(element.count)
                }
            });
            return countsum
        },
        addpricesum(){ //获取总的价格
            let pricesum=0
            this.shop.forEach(item=>{
                if(item.flag){
                     pricesum+=item.count*item.price1
                     }
            })
            return pricesum
        }
    },
    beforeRouteEnter:(to,from,next)=>{
        // if(this.$store.getters.getusename!=null){
        //     console.log(1)
        //     next('/login')
        // }
        next(vm=>{
            if(vm.$store.getters.getusename==null){
                if(confirm("请先登录")){
                    vm.$router.push("/login")
                }else{
                    vm.$router.push("/menu")
                }
            }
        })
    }
}
</script>
<style lang="scss" scoped>
.container{

   
    .btn-outline-danger{
        cursor: pointer;
        border: none;
        border-radius: 10px;
    }
    .row{
        background:red($color: #eeecec);
        margin-top: 10px;
       .col-8{
           .box{
               display: flex;
               justify-content: space-between;
           }
           h5{
               font-weight: bold;
           }
           .fl{
              display: inline-block;
              button{
                  background:white;
                  cursor: pointer;
                  border: none;
              }
              strong{
                  display: inline-block;
                  width: 40px;
              }
           }
           
       }
    }
}
</style>



