 <template>
    <div class="menu">
        <div class="row">
        <div class="col-sm-12 col-md-8">
            <table class="table">
                <thead class="thead-default">
                    <tr>
                        <th>尺寸大小</th>
                        <th>单价</th>
                        <th>添加</th>
                    </tr>
                </thead>
                <tbody v-for="(item,i) in this.$store.state.foots" :key="i">
                <tr>
                  <td><strong>{{item.name}}</strong></td>
                </tr>
                <tr v-for="(items,index) in item.options" :key="index">
                    <td>{{items.size1}}寸</td>
                    <td>{{items.price1}}元</td>
                    <td><button class="btn btn-sm btn-outline-success" @click="addToshop(item,items)">+</button></td>
                </tr>
                </tbody>
                <tr>
                    <td></td>
                </tr>
            </table>
        </div>
        <div v-show="this.$store.getters.counts<=0" class="col-sm-12 col-md-4 right">
          <table class="table text-center">
             <thead class="thead-default">
                 <tr>
                     <th colspan="3">你还没有添加任何购物商品！！！</th>
                 </tr>
             </thead>
             <tbody>
                 <tr>
                     <td colspan="3">请点击添加按钮</td>
                 </tr>
             </tbody>
        </table>
        </div>
       <div  v-show="this.$store.getters.counts>0" class="col-sm-12 col-md-4 right">
        <table class="table text-center">
             <thead class="thead-default">
                 <tr>
                     <th colspan="3">购物车</th>
                 </tr>
             </thead>
             <tbody>
                 <!-- <tr v-for="(item,i) in this.$store.state.addshop" :key="i">
                     <td>
                         <button @click="countjian(item)" class="btn btn-sm">-</button>
                         <span>1</span>
                         <button @click="countjia(item)" class="btn btn-sm">+</button>
                     </td>
                     <td>{{item.name}}{{item.size1}}</td>
                     <td>{{item.price1}}元</td>
                 </tr> -->
                 <tr>
                     <td colspan="2">数量:<span>{{this.$store.getters.counts}}</span><strong>件</strong></td>
                     <td><router-link to="/menu/addshop" tag="button" class="btn btn-sm btn-outline-success">查看购物车</router-link></td>
                 </tr>
             </tbody>
        </table>
        </div>
        </div>
    </div>
</template>
<script>
// import axios from 'axios'
export default {
    data(){
        return{
            foots:this.$store.state.foots
        }
    },
    created(){
     this.$store.commit('getdata')
    },
    computed:{
        sum(){
          return this.$store.getter.sum
        },
        getaddshop(){
             return JSON.stringify(this.$store.state.addshop) 
        }
       
    },
    methods:{
     addToshop(item,items){
         let addshop={
             id:item.id,
             name:item.name,
             size1:items.size1,
             price1:items.price1,
             count:1,
             flag:true
         }
         this.$store.commit('addToshop',addshop)
     }
    }
}
</script>

<style lang="scss" scoped>
*{
    padding: 0;
    margin: 0;
}
.btn{
    padding:4px 10px;
}
.qiu{
    width: 30px;
    height: 30px;
    background: red;
    border-radius: 50%;
    color:white;
    text-align:center;
    line-height: 20px;
    z-index: 10;
    position: absolute;
   
}
.col-md-4{
    // .btn{
    //     background: #fff;
    // }
    td{
        span{
            width: 18px;
            height: 18px;
            border-radius: 50%;
            background-color: red;
            display: inline-block;
            text-align: center;
            line-height: 16px;
             color: white;
        }
    }
}
.v-enter,
.v-leave-to{
    opacity: 0;
}
.v-enter-active,
.v-leave-active{
    transition: all 1s linear;
}
</style> 



