import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)
export const store=new Vuex.Store({
    state:{
        foots:[],//这是开始初始化获取数据
        addshop:JSON.parse(localStorage.getItem("addshops"))||[], //添加的piaa
        username:JSON.parse(localStorage.getItem("username"))||null,
        islogin:JSON.parse(localStorage.getItem("islogin"))||false
    },
    mutations:{
       getdata(state){//获得初始化数据并赋值给state中foots
        // localStorage.removeItem('addshops')
        axios.get("api/menu.json").then(res=>{
            if(res.status==200){
            const foot=res.data
            var arr=[]
            for(let key in foot){
                foot[key].id=key
                arr.push(foot[key])
            }
            state.foots=arr
            }
        })
       },
       del(state,id){//在管理页面中删除
        state.foots.splice( state.foots.findIndex(foos=>{
               return foos.id==id }),1)
        state.addshop.forEach((item,i)=>{
            if(item.id==id){
                state.addshop.splice(i,1)
            }
        })
        localStorage.setItem('addshops',JSON.stringify(state.addshop))
        state.addshop.forEach((item,i)=>{
            if(item.id==id){
                state.addshop.splice(i,1)
            }
        })
        localStorage.setItem('addshops',JSON.stringify(state.addshop))
        
         axios.delete("api/menu/"+id+".json").then(res=>console.log(res))   
       },
       addToshop(state,addshops){//添加piaa物品
        let flag=false
        
           state.addshop.some(add=>{
               if(add.id==addshops.id&&add.size1==addshops.size1){
                     add.count++
                     flag=true
                     add.flag=true
                     return true
               }
           })
           if(!flag){
               state.addshop.push(addshops)
           }
           localStorage.setItem('addshops',JSON.stringify( state.addshop))
       },
       addfoots(state,adds){//添加新的piaa品种
          let trunoff=state.foots.some(addfoo=>{
           return addfoo.name==adds.name
          })
          if(!trunoff){
               axios.post("api/menu.json",adds).then(res=>{
                state.foots.push(adds)
               })
          }else{
              return trunoff
          }
       },
       shopadd(state,data){//在addshop增加物品的数量
             if(state.addshop.some(item=>{
                 if(item.id==data.id&&item.size1==data.size1){
                     item.count++
                     return true
                 }
             })){
                 localStorage.setItem('addshops',JSON.stringify(state.addshop))
                }
       },
       shopshin(state,data){ //在addshop减少物品的数量
       const trunoff=state.addshop.some(item=>{
          if(item.id==data.id&&item.size1==data.size1){
                  item.count--
                  if(item.count<=0){
                      item.count=0
                  }
                  return true
               } 
           })
           if(trunoff){localStorage.setItem('addshops',JSON.stringify(state.addshop))}
           
       },
       delshop(state,data){//在addshop删除物品
          if(state.addshop.some((item,i)=>{
               if(item.id==data.id&&item.size1==data.size1){
                   state.addshop.splice(i,1)
                  return true
               }
           })){
               localStorage.setItem('addshops',JSON.stringify(state.addshop))
           }
       },
       trueorfalse(state,data){//在addshop中提交后发生的事件
           state.addshop.some(item=>{
              for(let key in data){
                  if(item.id==data[key].id&&item.size1==data[key].size1){
                      item.flag=false
                  }
              }
           })
           localStorage.setItem('addshops',JSON.stringify(state.addshop))
       },
      truns(state,checks){//在addshop中的勾选事件
         if(state.addshop.some(item=>{
             if(item.id==checks.id&&item.size1==checks.size1){
                 item.flag=!item.flag
                 return true
             }
         })){
            localStorage.setItem('addshops',JSON.stringify(state.addshop))
         }
      },
      reset(state){
         state.username=null
         state.islogin=false
         localStorage.setItem('username',JSON.stringify(state.username))
         localStorage.setItem('islogin',JSON.stringify(state.islogin))
      },
      login(state,usepa){
        
        // axios.get("api/users.json").then(res=>{
        //     const user= res.data
        //     const arr=[]
        //  for(let key in user){
        //      arr.push(user[key]) 
        //  }
        //  if(arr.some((user)=>{
        //     return user.username==usepa.username &&user.password==usepa.Password
        //  })){
        //      alert("登录成功")
        console.log(usepa)
             if(usepa){state.username=usepa.username
             state.islogin=true}else{
                 state.username=null
                 state.islogin=false
             }
            //  this.$router.push({name:'homelink'})
            // next(vm=>{
            //     vm.push({name:'homelink'})
            // })
             localStorage.setItem('username',JSON.stringify(state.username))
             localStorage.setItem('islogin',JSON.stringify(state.islogin))
        //  }
        //  })
      }


    },
    getters:{
       sum(state){
           let sums=0
           state.addshop.forEach(sumss=>{
              sums=sums+sumss.price1
           })
           return sums
       },
       counts(state){//在menu中获取总数量
           let sumcount=0
           if(state.addshop.length>0){
                state.addshop.forEach(item=>{
                    if(item.flag){sumcount+=item.count}  
                })
           }
          
            return sumcount
       },
       getdelpiaa(state){
          return state.foots
       },
       getaddshop(state){
        return state.addshop
       },
       getusename(state){
        return state.username
       },
       getislogin(state){
       return  state.islogin
        }
    }
})


