<template>
    <div class="register">
        <div class="alert alert-warning text-center" role="alert" v-if="flag">
             两次密码不一致！！！ 
        </div>
        <div class="row mt-2">
            <div class="col-lg-12 col-md-12 border nk">
                <div class="text-center card-body">
                 <img src="../assets/logo.png">
                </div>
                <form @submit.prevent>
                <label class="text-left" >邮箱</label>
                <input type="email" v-model="username">
                <label class="text-left">密码</label>
                <input type="password" v-model="Password">
                <label class="text-left">再次输入密码</label>
                <input type="password" v-model="confirPassword">
                <button @click="register" class="btn btn-block btn-success text-center">注册</button>
                </form>
                
            </div>
        </div>
    </div>
</template>
<script>
// import axios from 'axios'
export default {
    data(){
        return{
            username:'',
            Password:'',
            confirPassword:'',
            flag:false
        }
    },
    created(){

    },
    methods:{
        register(){
            if(this.Password==this.confirPassword){
              
              this.flag=false;
              const fromName={
                   username:this.username,
                   password:this.Password
               }
             this.axios.post("/api/users.json",fromName).then(res=>{alert("注册成功！！！")
             this.$router.push('/login')
             });
            }else{
              this.flag=true;
            }
           
        }
    }
}
</script>

<style lang="scss" scoped>
*{
    padding: 0;
    margin: 0;
}
.nk{
    box-sizing:border-box;
}
label{
    display: block;
    margin: 10px 0;
    font-weight:bold;
}
input{
    display: block;
    width: 100%;
    padding-left: 6px;
}
form{
    padding:10px;
}
button{
    border-radius:2px;
    margin-top: 10px;
    padding: 4px 0;
   
}
</style>




