import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import vuex from 'vuex'
import axios from 'axios'
import {store} from './store/store.js'
 
// Vue.use(axios)
Vue.use(vuex)
axios.defaults.baseURL="https://pipai-277a6.firebaseio.com/"
Vue.prototype.axios=axios

import Home from './components/Home'
import Admin from './components/Adimn'
import Login from './components/Login'
import Menu from './components/Menu'
import Register from './components/Register'
import About from './components/about/about'
import AddShop from './components/menuchild/addshop'

//二级路由
import History from './components/about/history'
import Contact from './components/about/contact'
import OderingGuide from './components/about/orderingGuide'
import Delivery from './components/about/delivery'
//三级路由
import Person from './components/about/contacts/Person'
import Tel from './components/about/contacts/Tel'

 Vue.use(VueRouter)
 const router=new VueRouter({
  mode:'history',
  routes:[
    {path:'/',name:'homelink',component:Home},
    {path:'/admin',name:'adminlink',component:Admin},
    {path:'/login',name:'loginlink',component:Login},
    {path:'/menu',name:'menulink',component:Menu},
    {path:'/menu/addshop',name:'addshop',component:AddShop},
    {path:'/register',name:'registerlink',component:Register},
    {path:'/about',name:'aboutlink',redirect:"/contact", component:About,children:[
      {path:'/history',name:'historylink',component:History},
      {path:'/contact',name:'contactlink',redirect:"/contact/personname", component:Contact,children:[
        {path:'/contact/personname',name:'personnamelink',component:Person},
        {path:'/contact/tel',name:'tellink',component:Tel}
      ]},
      {path:'/orderingGuide',name:'orderingGuidelink',component:OderingGuide},
      {path:'/delivery',name:'deliverylink',component:Delivery},
    ]},
    {path:'*',redirect:'/'}
  ],
  linkActiveClass:'actives'
})
new Vue({
  el: '#app',
  render: h => h(App),
  router,
  store
})

