<template>
    <div class="orderingGuide">
    <div class="text-center">
        <h2>欢迎查看本店点餐文档</h2>
    </div>
    <div class="card" v-for="(item,i) in shop" :key="i">
        <div class="card-body">
        <h5 class="card-title">{{item.name}}</h5>
        <h6 class="card-subtitle mb-2 text-muted" v-for="(items,j) in item.options" :key="j">大小：{{items.size1}}寸------------价格：{{items.price1}}元/份</h6>
        <a href="#" class="card-link">描述：{{item.description}}</a>
        <a href="#" class="card-link" ref="timeout"></a>
  </div>
</div>
    </div>
</template>
<script>
export default {
   data(){
     return{

     }
   },
   created(){
   this.$store.commit('getdata')
   },
   computed:{
       shop(){
          return this.$store.state.foots
       }
   }

}
</script>
<style lang="scss" scoped>
 .card-link{
    color: black;
 }
</style>




