<template>
    <div class="history">
        
        <div class="card" v-for="(item,i) in history" :key="i">
        <div class="card-body">
        <h5 class="card-title">品种：{{item.name}}</h5>
        <h6 class="card-subtitle mb-2 text-muted">大小：{{item.size1}}寸</h6>
        <p class="card-text">购买的数量：{{item.count}}</p>
        <!-- <a href="#" class="card-link">Card link</a> -->
        <a href="#" class="card-link" ref="timeout">购买时间：{{item.times | time}}</a>
  </div>
</div>
    </div>
</template>
<script>
export default {
    data(){
        return{
           history:[]
        }
    },
    created(){
        this.axios.get('api/postshop.json').then(res=>{//获取购买的历史记录
            let arr=[]
          for(let key in res.data){
              arr=arr.concat(res.data[key])
          }
         arr=arr.filter(arrs=>{
              return arrs!=null
          })
          this.history=arr
        }).catch(res=>{
            console.log("获取数据异常")
        })
        // this.$refs.timeout.style.m
    },
    filters:{
         time(el){//时间的过滤
             let tiem=new Date(el)
             var n=tiem.getFullYear()
             var y=tiem.getMonth()+1
             var r=tiem.getDate()
             var s=tiem.getHours()
             var f=tiem.getMinutes()
             var m=tiem.getSeconds()
             return `${n}-${y}-${r} ${s}:${f}:${m}`
         }
    }
    
}
</script>
<style lang="scss" scoped>
.history{
    .card{
        margin-top: 10px;
       .card-link{
           float: right;
       }
    }
}

</style>




