<template>
    <div class="delpiaa">
      <table class="table">
        <thead  class="table table-default">
             <tr>
             <th>品种</th>
             <th>删除</th>
            </tr>
        </thead>
        <tbody class="table table-default" v-for="item in this.$store.getters.getdelpiaa" :key="item.id">
          <tr>
              <td>{{item.name}}</td>
              <td><button class="btn btn-sm btn-outline-warning" @click="del(item.id)">&times;</button></td>
          </tr>
        </tbody>
      </table>
    </div>
</template>
<script>
export default {
    data(){
        return{
        
        }
    },
    created(){
        // this.axios.get("api/menu.json").then(res=>{
        //    const fo= res.data
        //    const arr=[]
        //    for(let key in fo){
        //         fo[key].id=key
        //         arr.push(fo[key])
        //    }
        //    this.delpiaa=arr;
        // })
        this.$store.commit('getdata')
    },
    methods:{
        del(id){
            // this.axios.delete("api/menu/"+id+".json").then(res=>console.log(res))
            this.$store.commit('del',id)
        }
    }
}
</script>

<style lang="scss" scoped>
*{
    padding: 0;
    margin: 0;
}
.btn{
    padding:4px 8px;
}
</style>




